import { LitElement, html, css, nothing, TemplateResult } from 'lit';
import { customElement, property, query, queryAll } from 'lit/decorators.js'
import './inputs';
import { DayInputsComponent } from './inputs';
import { DayOfWeek_I } from '@@addons/interfaces/attendance/day_of_week';
import '@material/mwc-button';
import '@material/mwc-circular-progress';
import { MeetingEventFormInputInterface } from '@@addons/interfaces/attendance/meeting_event/day';

@customElement("day-multiple-inputs-component")
export class DayMultipleInputsComponent extends LitElement {
  constructor() { super(); }

  @property({ type: Number })
  public meetingEventId: number;

  @property({ type: Number })
  public currentObjectCount: number = 0;

  @property({ type: Array })
  public multipleDayObjects: MeetingEventFormInputInterface[] = [];

  @property({ type: Array })
  private removedInputIndex: number[] = [];

  @property({ type: Array })
  private addedInputIndex: number[] = [];

  @property({ type: Boolean })
  private daysOfWeekCalled: boolean = false;

  @property({ type: Boolean })
  private loading: boolean = true;

  @property({ type: Array })
  public daysOfWeek: DayOfWeek_I[] = [];

  // @property({ type: Array })
  // private inputs: Array<DayInputsComponent> = [];

  // private _inputs: Array<DayInputsComponent> = [];

  // private set inputs(value: Array<DayInputsComponent>) {
  //   this._inputs = value;
  //   this.requestUpdate();
  // }

  // private get inputs(): Array<DayInputsComponent> {
  //   return this._inputs;
  // }

  private _inputs: Array<TemplateResult> = [];

  private set inputs(value: Array<TemplateResult>) {
    this._inputs = value;
    this.requestUpdate();
  }

  private get inputs(): Array<TemplateResult> {
    return this._inputs;
  }

  connectedCallback() {
    super.connectedCallback();

    // document.onreadystatechange = (event) => {
    //   console.log({"event-event": event});

    //   this.addInput(new Event('click'));
    // //   if (document.readyState == "complete") this.addInput(new Event('click'));
    // }


    setInterval(() => {
      if (this.daysOfWeekCalled == false && this.daysOfWeek.length > 0) {
        this.addInput(new Event('click'));

        this.loading = false;
        this.daysOfWeekCalled = true;
      }
    }, 1);
  }

  disconnectedCallback() { }
  static styles = [
    css`
   :host { display: block; }
  `
  ];

  // addInput(event: Event) {
  //   let newInput = new DayInputsComponent();
  //   newInput.daysOfWeek = this.daysOfWeek;
  //   // console.log({ "newInput-newInput": newInput });

  //   let inputs = this.inputs;
  //   inputs.push(newInput);

  //   setTimeout(() => {
  //     this.inputs = inputs;
  //   }, 10);
  //   // console.log({ "newInput-this.inputs": this.inputs });
  // }

  addInput(event: Event) {
    this.currentObjectCount = this.currentObjectCount + 1;
    let newInput = html`<day-inputs-component day-inputs-component--object="${this.currentObjectCount}" 
      .daysOfWeek="${this.daysOfWeek}" >
    </day-inputs-component>`;
    // newInput.daysOfWeek = this.daysOfWeek;
    // console.log({ "newInput-newInput": newInput });

    let inputs = this.inputs;
    inputs.push(newInput);

    let addedInputIndex = this.addedInputIndex;
    addedInputIndex.push(this.currentObjectCount - 1);

    setTimeout(() => {
      this.inputs = inputs;
      this.addedInputIndex = addedInputIndex;
      this.multipleInputsHaveChanged(new Event("change"))
    }, 10);
    // console.log({ "newInput-this.inputs": this.inputs });
  }

  removeInput(index: number) {
    // console.log({ "this.inputs": this.inputs });


    if (this.currentInputList.length - 1 > 0) {
      let inputs = this.inputs;

      this.removedInputIndex.push(index);

      delete inputs[index];
      // inputs.splice(index, 1);

      setTimeout(() => {
        this.inputs = inputs;

        this.multipleInputsHaveChanged(new Event("change"))
      }, 10);
    }
  }

  get currentInputList() {
    let currentList: Array<any> = [];

    const inputs = this.inputs;

    inputs.join("-").split("-").forEach(_ => _.length == 0 ? null : currentList.push(_));

    return currentList;
  }

  render() {
    // console.log({ "inputs-inputs.inputs": this.inputs });
    return this.loading ? html`<div class="flex justify-center">
        <mwc-circular-progress indeterminate></mwc-circular-progress>
      </div>` : html`
      <div @change="${this.multipleInputsHaveChanged}">
          ${this.inputs.map((input, index) => html`
              <div class="border rounded-md mb-1">
                <h3 class="shadow p-3 font-bold">Day Info ${index + 1}</h3>
                <!-- ${"" /** input.render()  */} -->
                ${input}
                ${(this.currentInputList.length - 1 > 0) ? html`<div class="flex justify-end">
                  <mwc-button @click=${() => this.removeInput(index)} type="button" class="button danger text-right m-1" raised>Remove</mwc-button>
                  </div>` : nothing}
              </div>
            `,)}
        <div class="flex justify-end">
          <mwc-button @click=${this.addInput} type="button" class="button warning text-right m-1" raised>Add Meeting/ Event Day</mwc-button>
        </div>
      </div>
    `;
  }

  multipleInputsHaveChanged(event: Event) {

    this.addedInputIndex

    const inputComponents = Array.from(document.querySelectorAll('[day-inputs-component--object]'));
    // console.log({ inputComponents, "this.removedInputIndex": this.removedInputIndex });

    const notRemovedComponents: Array<any> = [];

    this.addedInputIndex.forEach((inputIndex) => {

      if (!this.removedInputIndex.includes(inputIndex)) {

        if (!notRemovedComponents.includes(inputIndex)) {
          notRemovedComponents.push(inputIndex);
        }
      }
    });

    // console.log({notRemovedComponents});


    this.multipleDayObjects = notRemovedComponents.map((input) => {
      const inputComponent = Array.from(document.querySelectorAll('[day-inputs-component--object="' + (input + 1) + '"]'));
      // console.log({ "inputComponent---inputComponent": inputComponent, input });

      return inputComponent.map(inputElement => {
        // console.log({ "inputElement---inputElement": inputElement });

        let newObject = new MeetingEventFormInputInterface();
        newObject.meetingEventId = this.meetingEventId;
        // @ts-ignore
        newObject.dayId = Number(inputElement.dayValue ?? 0);
        // @ts-ignore
        newObject.startDate = inputElement.startDateValue ?? "";
        // @ts-ignore
        newObject.endDate = inputElement.endDateValue ?? "";
        return newObject;
      })[0];
    });
 

    // console.log({ "multiple_day_objects": this.multipleDayObjects });

  }

  multipleInputsHaveChangedOld(event: Event) {
    // console.log({ "event-event--event": event });

    // this.multipleDayObjects = this.inputs.map((input) => {
    //   console.log({ "input---input": input });
    //   console.log({ "input---input.id": document.querySelectorAll('[day-inputs-component--object]') });

    //   let newObject = new MeetingEventFormInputInterface();
    //   newObject.meetingEventId = this.meetingEventId;
    //   newObject.dayId = Number(input.dayValue);
    //   newObject.startDate = input.startDateValue;
    //   newObject.endDate = input.endDateValue;
    //   return newObject;
    // });

    const inputComponents = Array.from(document.querySelectorAll('[day-inputs-component--object]'));
    // console.log({ inputComponents, "this.removedInputIndex": this.removedInputIndex });

    // const notRemovedComponents: Array<any> = [];

    // inputComponents.forEach((inputComponent, i) => {
    //   // console.log({'this.removedInputIndex.indexOf(i)': this.removedInputIndex.indexOf(i)});

    //   const indexExists = this.removedInputIndex[i] !== undefined;

    //   if (!indexExists) {
    //     notRemovedComponents.push(inputComponent);
    //   }
    // });

    // console.log({notRemovedComponents});


    this.multipleDayObjects = inputComponents.map((input) => {
      // this.multipleDayObjects = notRemovedComponents.map((input) => {
      // console.log({ "input---input": input });

      let newObject = new MeetingEventFormInputInterface();
      newObject.meetingEventId = this.meetingEventId;
      // @ts-ignore
      newObject.dayId = Number(input.dayValue);
      // @ts-ignore
      newObject.startDate = input.startDateValue;
      // @ts-ignore
      newObject.endDate = input.endDateValue;
      return newObject;
    });

    // console.log({ "multiple_day_objects": this.multipleDayObjects });

  }

  firstUpdated() {

  }

  createRenderRoot() {
    return this;
  }
}
